import React, { useState, useEffect } from 'react';
import '../../../App.css';
import {Link} from "react-router-dom";
import '../../../App.css';
import logo from '../../../assets/images/logo.png';

import './Navbar.css';
import LanguageSwitcher from "../LanguageSwitcher/LanguageSwitcher";

function Navbar() {
    const [click, setClick] = useState(false);
    const [servicesDropdown, setServicesDropdown] = useState(false);
    const [projectsDropdown, setProjectsDropdown] = useState(false);

    const handleServicesMouseEnter = () => {
        setServicesDropdown(true);
    };

    const handleServicesMouseLeave = () => {
        setServicesDropdown(false);
    };

    const handleProjectsMouseEnter = () => {
        setProjectsDropdown(true);
    };

    const handleProjectsMouseLeave = () => {
        setProjectsDropdown(false);
    };

    return (
        <>
            <nav className="navbar">
                <div className="navbar-container">
                    <Link to="/" className="navbar-logo">
                        <img src={logo} alt="Logo" className="navbar-logo-image"/>
                    </Link>
                    <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                        <li className="nav-item">
                            <Link to="/" className="nav-links">
                                Home
                            </Link>
                        </li>
                        <li
                            className="nav-item"
                            onMouseEnter={handleProjectsMouseEnter}
                            onMouseLeave={handleProjectsMouseLeave}
                        >
                            <Link to="/projects" className="nav-links">
                                Projects
                            </Link>
                            {projectsDropdown && (
                                <ul className="dropdown-menu">
                                    <li className="dropdown-item">
                                        <Link to="/projects/commercial" className="dropdown-link">Commercial</Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/projects/residential" className="dropdown-link">Residential</Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/projects/hotels" className="dropdown-link">Hotels</Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/projects/constructions"
                                              className="dropdown-link">Constructions</Link>
                                    </li>
                                </ul>
                            )}
                        </li>
                        <li
                            className="nav-item"
                            onMouseEnter={handleServicesMouseEnter}
                            onMouseLeave={handleServicesMouseLeave}
                        >
                            <Link to="/services" className="nav-links">
                                Services
                            </Link>
                            {servicesDropdown && (
                                <ul className="dropdown-menu">
                                    <li className="dropdown-item">
                                        <Link to="/services/olikes-anakainiseis" className="dropdown-link">
                                            </Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/drywall" className="dropdown-link"></Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/special-constructions" className="dropdown-link">
                                            </Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/floor" className="dropdown-link"></Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/bathroom" className="dropdown-link"></Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/kitchen" className="dropdown-link"></Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/garden" className="dropdown-link"></Link>
                                    </li>
                                    <li className="dropdown-item">
                                        <Link to="/services/cleaning-services" className="dropdown-link">
                                            </Link>
                                    </li>
                                </ul>
                            )}
                        </li>
                        <li className="nav-item">
                            <Link to="/company" className="nav-links">
                                Company
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/contact" className="nav-links">
                                Contact
                            </Link>
                        </li>
                        <li className="language-selector">
                            <LanguageSwitcher/>
                        </li>
                    </ul>
                </div>
            </nav>
        </>

    )
}

export default Navbar
